import React, {Component, useState} from 'react'
import { Table, Button, Modal } from 'antd';
import { CheckCircleTwoTone } from '@ant-design/icons';
import OrderDetail from './order-detail';

import './order.css'
import {gql} from "apollo-boost";
import {Query} from "react-apollo";
import Error from "../shared/Error";
import Loading from "../shared/loading";


const columns = [
    {
      title: ' ',
      dataIndex: 'orderer',
    },
    {
      title: '',
      dataIndex: 'deliverer',
    },
    {
      title: '',
      dataIndex: 'address',
      filters: [
        {
          text: '',
          value: 'Tehran',
        },
        {
          text: '',
          value: 'Shiraz',
        },
      ],
      // TODO: make it work
      onFilter: (value, record) => record.address.indexOf(value) === 0,
    },
    {
      title: '',
      dataIndex: 'status',
        filters: [
        {
          text: " ",
          value: 'submitted',
        },
        {
          text: "  " ,
          value: 'loading',
        },
        {
          text: "  ",
          value: 'sending',
        },
        {
          text: " ",
          value: 'delivered',
        },

      ],
      onFilter: (value, record) => record.address.indexOf(value) === 0,
    },
    {
      title: '',
      dataIndex: 'cost',
    },
];

const OrderList = ({customer}) => {
    const title = () => '  ';

    const [visible, setVisible] = useState(false);


    const state = {
        visible: false,
        bordered: true,
        loading: false,
        pagination: false,
        size: 'default',
        title,
        showHeader: true,
        rowSelection: {},
        scroll: undefined,
        tableLayout: undefined,
        top: 'none',
        bottom: 'bottomRight',
      };

    
    const allInfo = [];



    return (
        <Query query={GET_CUSTOMER_ORDERS} variables={{"id": customer.id}}>
            {({data, loading, error}) => {
                if (loading) return <Loading/>;


                {
                    data.customerOrders.map(order => {
                        allInfo.push(
                            {
                                kay: order.id,
                                orderer: order.owner.user.firstName + " " + order.owner.user.lastName,
                                deliverer: order.driver.user.firstName + " " + order.driver.user.lastName,
                                address: order.destinationAddress,
                                cost: order.transportationCost,
                                status: order.orderStatus === "A_1" ? " " : order.orderStatus === "A_2" ? "  " : order.orderStatus === "A_3" ? "  ":  order.orderStatus === "A_4" ? " " : "",
                            }
                        );
                        console.log(allInfo);

                    })
                }
                console.log(allInfo);
                return (
                    <div className="order-container">
                        <Table
                            {...state}
                            columns={columns}
                            dataSource={allInfo}
                        />
                        {error && <Error error={error} />}
                    </div>

                );
            }}
        </Query>
    )
};

const GET_CUSTOMER_ORDERS = gql`
query ($id : ID!){
    customerOrders(id : $id) {
        id,
        owner{
            user{
                firstName,
                lastName
            }
        },
        driver{
            user{
                firstName,
                lastName
            }
        },
        destinationAddress,
        orderCode,
        transportationCost,
        orderStatus
    }
}
`;


export default (OrderList)
