import React, { useState } from "react";
import {Modal} from "antd";

const Error = ({ error }) => {
  const [visible, setVisible] = useState(true);

  return (
   <Modal
        title = "    "
        visible={visible}
        onOk = {() => {
            setVisible(false);
          }
        }
        cancelButtonProps={{ type: "text"}}
        okText=""
        cancelText=" "
        >
       <p>
            :
           {error.message}
       </p>
   </Modal>
  )
};


export default (Error);