import React, { useState } from 'react'
import { Table, Button, Modal, Tooltip, Input } from 'antd';
import {EditOutlined, KeyOutlined, UserOutlined, MinusOutlined} from '@ant-design/icons';
import Error from "../shared/Error";
import Loading from "../shared/loading"


import '../user/user.css'
import {Mutation, Query} from "react-apollo";
import {gql} from "apollo-boost";
import {UserType} from "../shared/user-type-enum";
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import Nav from "../../admin-root";
import EditCustomer from "./edit-customer"


export const UserContext = React.createContext();

const columns = [
    {
      title: '',
      dataIndex: 'firstName',
    },
    {
      title: ' ',
      dataIndex: 'lastName',
    },
    {
      title: '',
      dataIndex: 'history',

    },
    {
      title: ' ',
      dataIndex: 'birthday',
    },
    {
      title: ' /  ',
      dataIndex: 'editDelete',
    }
];

const { Search } = Input;
const title = () => (
    <div>
        <p>
             
        </p>
        <Search
            placeholder="   "
            onSearch={value => console.log(value)}
            enterButton
            style={{width:400}}
        />
    </div>
);


const AllCustomersList = ({customer}) => {
    const [visibleHistory, setVisibleHistory] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [visibleDelete, setVisibleDelete] = useState(false);
    const [customerClone, setCustomerClone] = useState(null);

    const state = {
        bordered: true,
        loading: false,
        pagination: false,
        size: 'default',
        title,
        showHeader: true,
        rowSelection: {},
        scroll: undefined,
        tableLayout: undefined,
        top: 'none',
        bottom: 'bottomRight',
    };


    const handleEdit = (customer) => {
        console.log(customer);
        setCustomerClone(customer);
        setVisibleEdit(true);
    };

    const handleDelete = (event, deleteCustomer) => {
        event.preventDefault();
        deleteCustomer();
    };



    const allInfo = [];

    function info(message, content) {
      Modal.info({
        title: message,
        content: content,
        onOk() {},
      });
    }


    return (

        <Query query={GET_ALL_CUSTOMERS}>
        {({data, loading, error}) => {
            if(loading) return <Loading/>;
            console.log(data);

            const orderColumns = [
                {
                  title: ' ',
                  dataIndex: 'orderLocation',
                },
            ];

            const orderInfo = [];


            {data.allCustomers.map( customer => {
                {customer.orders.map( order=> {
                    orderInfo.push({orderLocation: "(" + order.latitude+ ", " + order.longitude + ")"})
                })}
                allInfo.push({
                    key: customer.id,
                    firstName:
                        customer.user.firstName,
                    lastName:
                        customer.user.lastName,
                    birthday:
                        customer.birthday,
                    location: <Button key={4*customer.id} >  </Button>,
                    history:
                        <div>
                            <Button key={4*customer.id + 1} onClick={() =>
                                info("  ",
                                    <Table
                                    columns={orderColumns}
                                    dataSource={orderInfo}/>)} >
                                 
                            </Button>
                        </div>,
                    editDelete:
                    <div>
                        <Tooltip placement="top" title=''>

                            <Button key={4* customer.id + 2} shape="circle" onClick={() => handleEdit(customer)}>
                                <EditOutlined />
                            </Button>

                        </Tooltip>
                        <Mutation mutation={REMOVE_MUTATION}
                          variables={
                              {
                                  "id" : customer.id
                              }
                          }
                          onCompleted={() => {
                              info("    ")
                          }}
                        >{(deleteCustomer, deleteData) => {
                            return (

                                <Tooltip placement="top" title= {deleteData.loading ? "   ..." : " "}>
                                    <Button key={4* customer.id + 3} shape="circle" onClick={event => handleDelete(event, deleteCustomer)} disabled = {deleteData.loading}>
                                        <MinusOutlined />
                                    </Button>
                                    {deleteData.error && <Error error={deleteData.error} />}
                                </Tooltip>
                            )
                        }}
                        </Mutation>
                    </div>

                });

            })}
            return (
                <div className="order-container">



                    <Table
                        {...state}
                        columns={columns}
                        dataSource={allInfo}
                    />
                    {error && <Error error = {error}/>}
                    {customerClone && <EditCustomer customer = {customerClone} visible = {visibleEdit} setVisible = {setVisibleEdit}/>}
                </div>
            )
        }}
        </Query>
    )
};


const GET_ALL_CUSTOMERS = gql`
{
    allCustomers {
        id
        user{
            id
            firstName
            lastName
            username
            password
            email
            phoneNo
        }
        birthday
        orders {
            longitude
            latitude
        }
    }
}
`;



const REMOVE_MUTATION = gql`
mutation($id : ID!){
    deleteCustomer (id : $id){
      id
    }
}
`;

export default (AllCustomersList);