import React, { useState } from 'react'
import { Component } from 'react'
import { Mutation } from "react-apollo";
import { gql } from "apollo-boost";
import Error from "../shared/Error";

import {
    Form,
    Input,
    Button,
    Upload,
    DatePicker, Modal,
} from 'antd';
import { UploadOutlined } from '@ant-design/icons';
// import { useQuery, gql, useMutation } from "@apollo/client";

import './user.css'



const formItemLayout = {
    labelCol: { span: 6 },
    wrapperCol: { span: 14 },
};


const DriverRegister = () => {
  const [username, setUsername] = useState("");
  const [usernameState, setUsernameState] = useState(false);
  const [email, setEmail] = useState("");
  const [emailState, setEmailState] = useState(false);
  const [password, setPassword] = useState("");
  const [passwordState, setPasswordState] = useState(false);
  const [firstName, setFirstName] = useState("");
  const [firstNameState, setFirstNameState] = useState(false);
  const [lastName, setLastName] = useState("");
  const [lastNameState, setLastNameState] = useState(false);
  const [nationalId, setNationalId] = useState("");
  const [NationalIdState, setNationalIdState] = useState(false);
  const [birthDay, setBirthDay] = useState(null);
  const [birthDayState, setBirthDayState] = useState(false);
  const [phoneNo, setPhoneNo] = useState("");
  const [phoneNoState, setPhoneNoState] = useState(false);
  const [visible, setVisible] = useState(false);
  const [image, setImage] = useState(null);
  const [imageState, setImageState] = useState(false);


  const handleSubmit = (event, createDriver) => {
      console.log("In the handleSubmit");
      event.preventDefault();
      createDriver();
  };

  const normFile = e => {
    console.log('Upload event:', e.file);
    setImage(e.file);
    if (Array.isArray(e)) {
        return e;
    }
    return e && e.fileList;
  };


  function info(message) {
      Modal.info({
        title: message,
        onOk() {},
      });
  }

  return (
    <div>
        <Mutation
          mutation={REGISTER_MUTATION}
          variables={
              {
                  "driverData": {
                      "user": {
                          "firstName": firstName,
                          "lastName": lastName,
                          "username": username,
                          "email": email,
                          "phoneNo": phoneNo,
                          "password": password
                      },
                      "nationalId": nationalId
                      // "birthday": birthDay
                  }
              }
          }

          onCompleted={data => {
            info("           ")
          }}
        >
          {(createDriver, { loading, error }) => {
            return (
              <Form
                className="registration-form"
                name="user registration"
                {...formItemLayout}
                // onSubmit={event => handleSubmit(event, createUser)}
              >


                  <Form.Item label="">
                    <Input
                        id = "name"
                        onChange={event => {
                                setFirstName(event.target.value);
                                setFirstNameState(true);
                            }
                        }
                        placeholder=""/>
                  </Form.Item>

                  <Form.Item label=" ">
                    <Input
                        id = "familyName"
                        onChange={event => {
                                setLastName(event.target.value);
                                setLastNameState(true);
                            }
                        }
                        placeholder=""/>
                  </Form.Item>

                  <Form.Item label=" ">
                    <Input
                        id = "username"
                        onChange={event => {
                                setUsername(event.target.value);
                                setUsernameState(true);
                            }
                        } />

                  </Form.Item>

                  <Form.Item label=" ">
                    <Input
                        id = "password"
                        onChange={event => {
                                setPassword(event.target.value);
                                setPasswordState(true);
                            }
                        }/>

                  </Form.Item>

                  <Form.Item label="" >
                    <Input
                        id = "email"
                        onChange={event => {
                            if(event.target.value.match("^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+[.][A-Za-z]{2,}$") != null){
                                setEmail(event.target.value);
                                setEmailState(true);
                                document.getElementById("email").style.backgroundColor = 'white';
                            }
                            else {
                                setEmailState(false);
                                document.getElementById("email").style.backgroundColor = 'pink';
                            }
                        }}/>
                  </Form.Item>

                  <Form.Item label=" ">
                    <Input
                        id = "phoneNumber"
                        onChange={event => {
                            if(event.target.value.match("^[0-9]{8,11}$") != null){
                                setPhoneNo(event.target.value);
                                setPhoneNoState(true);
                                document.getElementById("phoneNumber").style.backgroundColor = 'white';
                            }
                            else {
                                setPhoneNoState(false);
                                document.getElementById("phoneNumber").style.backgroundColor = 'pink';
                            }
                        }}
                        placeholder= "09092929912"/>
                    </Form.Item>

                  <Form.Item label=" ">
                    <Input
                        id = "nationalId"
                        onChange={event => {
                            if(event.target.value.match("^[0-9]{10}$") != null){
                                setNationalId(event.target.value);
                                setNationalIdState(true);
                                document.getElementById("nationalId").style.backgroundColor = 'white';
                            }
                            else {
                                document.getElementById("nationalId").style.backgroundColor = 'pink';
                                setNationalIdState(false);
                            }
                        }}
                        placeholder="0020996519"/>
                  </Form.Item>

                  <Form.Item label=" ">
                    <DatePicker id = "birthDay" onChange=
                        {date => {
                                setBirthDay(date);
                                setBirthDayState(true);
                            }
                        }/>
                  </Form.Item>

                  <Form.Item
                    name=" "
                    label=" "
                    valuePropName="fileList"
                    getValueFromEvent={normFile}
                >
                    <Upload name="logo" action="/upload.do" listType="picture">
                    <Button>
                        <UploadOutlined />     
                    </Button>
                    </Upload>
                </Form.Item>


                <Form.Item wrapperCol={{ span: 12, offset: 6 }}>
                    <Button
                        type="primary"
                        htmlType="submit"
                        disabled={
                            loading ||
                            !firstNameState ||
                            !lastNameState ||
                            !usernameState ||
                            !NationalIdState ||
                            !birthDayState||
                            !emailState ||
                            !passwordState ||
                            !phoneNoState
                        }
                        onClick={event => handleSubmit(event, createDriver)}
                    >
                        {loading ? "   ..." : " "}
                    </Button>
                    {/*{error && <Error error={error} />}*/}

                </Form.Item>
                {/* Error Handling */}
                {error && <Error error={error} />}
              </Form>
            );
          }}
        </Mutation>
    </div>
  );
};

const REGISTER_MUTATION = gql`
  mutation ($driverData: DriverInput!) {
  createDriver(driverData: $driverData) {
    driver{
       user{
        firstName
        lastName
       }
    }
  }
}
`;


export default (DriverRegister);